import { MAX_ENTITIES, resource, type Plugin, type State, type System } from "../../core";
import { setTraits, type FieldAccessor } from "../../core/component";
import { Compute, ComputePlugin, createEntityIdBuffer } from "../../standard/compute";
import {
    Render,
    RenderPlugin,
    DEPTH_FORMAT,
    registerDrawContributor,
    type DrawContributor,
    type DrawContext,
} from "../../standard/render";
import { Transform } from "../../standard/transforms";

export const LineData = {
    data: new Float32Array(MAX_ENTITIES * 12),
};

interface LineProxy extends Array<number>, FieldAccessor {}

function lineProxy(offset: number): LineProxy {
    const data = LineData.data;

    function getValue(eid: number): number {
        return data[eid * 12 + offset];
    }

    function setValue(eid: number, value: number): void {
        data[eid * 12 + offset] = value;
    }

    return new Proxy([] as unknown as LineProxy, {
        get(_, prop) {
            if (prop === "get") return getValue;
            if (prop === "set") return setValue;
            const eid = Number(prop);
            if (Number.isNaN(eid)) return undefined;
            return getValue(eid);
        },
        set(_, prop, value) {
            const eid = Number(prop);
            if (Number.isNaN(eid)) return false;
            setValue(eid, value);
            return true;
        },
    });
}

function colorProxy(): LineProxy {
    const data = LineData.data;

    function getValue(eid: number): number {
        const offset = eid * 12 + 8;
        const r = Math.round(data[offset] * 255);
        const g = Math.round(data[offset + 1] * 255);
        const b = Math.round(data[offset + 2] * 255);
        return (r << 16) | (g << 8) | b;
    }

    function setValue(eid: number, value: number): void {
        const offset = eid * 12 + 8;
        data[offset] = ((value >> 16) & 0xff) / 255;
        data[offset + 1] = ((value >> 8) & 0xff) / 255;
        data[offset + 2] = (value & 0xff) / 255;
        data[offset + 3] = 1;
    }

    return new Proxy([] as unknown as LineProxy, {
        get(_, prop) {
            if (prop === "get") return getValue;
            if (prop === "set") return setValue;
            const eid = Number(prop);
            if (Number.isNaN(eid)) return undefined;
            return getValue(eid);
        },
        set(_, prop, value) {
            const eid = Number(prop);
            if (Number.isNaN(eid)) return false;
            setValue(eid, value);
            return true;
        },
    });
}

function colorChannelProxy(channelIndex: number): LineProxy {
    const data = LineData.data;

    function getValue(eid: number): number {
        return data[eid * 12 + 8 + channelIndex];
    }

    function setValue(eid: number, value: number): void {
        data[eid * 12 + 8 + channelIndex] = value;
    }

    return new Proxy([] as unknown as LineProxy, {
        get(_, prop) {
            if (prop === "get") return getValue;
            if (prop === "set") return setValue;
            const eid = Number(prop);
            if (Number.isNaN(eid)) return undefined;
            return getValue(eid);
        },
        set(_, prop, value) {
            const eid = Number(prop);
            if (Number.isNaN(eid)) return false;
            setValue(eid, value);
            return true;
        },
    });
}

export const Line: {
    offsetX: LineProxy;
    offsetY: LineProxy;
    offsetZ: LineProxy;
    thickness: LineProxy;
    visible: LineProxy;
    opacity: LineProxy;
    color: LineProxy;
    colorR: LineProxy;
    colorG: LineProxy;
    colorB: LineProxy;
} = {
    offsetX: lineProxy(0),
    offsetY: lineProxy(1),
    offsetZ: lineProxy(2),
    thickness: lineProxy(3),
    visible: lineProxy(4),
    opacity: lineProxy(7),
    color: colorProxy(),
    colorR: colorChannelProxy(0),
    colorG: colorChannelProxy(1),
    colorB: colorChannelProxy(2),
};

setTraits(Line, {
    defaults: () => ({
        offsetX: 1,
        offsetY: 0,
        offsetZ: 0,
        thickness: 2,
        visible: 1,
        opacity: 1,
        color: 0xffffff,
    }),
    accessors: {
        offsetX: Line.offsetX,
        offsetY: Line.offsetY,
        offsetZ: Line.offsetZ,
        thickness: Line.thickness,
        visible: Line.visible,
        opacity: Line.opacity,
        color: Line.color,
        colorR: Line.colorR,
        colorG: Line.colorG,
        colorB: Line.colorB,
    },
});

export interface LinesConfig {
    scene: GPUBuffer;
    lines: GPUBuffer;
    entityIds: GPUBuffer;
    matrices: GPUBuffer;
    getCount: () => number;
}

export const lineShader = /* wgsl */ `
struct VertexOutput {
    @builtin(position) position: vec4<f32>,
    @location(0) color: vec4<f32>,
    @location(1) dist: f32,
    @location(2) halfWidth: f32,
}

struct Scene {
    viewProj: mat4x4<f32>,
    cameraWorld: mat4x4<f32>,
    ambientColor: vec4<f32>,
    sunDirection: vec4<f32>,
    sunColor: vec4<f32>,
    cameraMode: f32,
    cameraSize: f32,
    viewport: vec2<f32>,
}

struct LineData {
    offset: vec3<f32>,
    thickness: f32,
    visible: f32,
    _pad1: f32,
    _pad2: f32,
    opacity: f32,
    color: vec4<f32>,
}

@group(0) @binding(0) var<uniform> scene: Scene;
@group(0) @binding(1) var<storage, read> entityIds: array<u32>;
@group(0) @binding(2) var<storage, read> lines: array<LineData>;
@group(0) @binding(3) var<storage, read> matrices: array<mat4x4<f32>>;

@vertex
fn vs(@builtin(vertex_index) vid: u32, @builtin(instance_index) iid: u32) -> VertexOutput {
    let eid = entityIds[iid];
    let line = lines[eid];
    let transform = matrices[eid];

    let start = transform[3].xyz;
    let rotation = mat3x3<f32>(transform[0].xyz, transform[1].xyz, transform[2].xyz);
    let end = start + rotation * line.offset;

    let startClip = scene.viewProj * vec4(start, 1.0);
    let endClip = scene.viewProj * vec4(end, 1.0);

    let startNDC = startClip.xy / startClip.w;
    let endNDC = endClip.xy / endClip.w;

    let dir = endNDC - startNDC;
    let len = length(dir);
    let normDir = select(vec2(1.0, 0.0), dir / len, len > 0.0001);

    // Convert thickness from pixels to NDC (2 pixels / viewport = NDC units)
    let halfWidth = line.thickness * 0.5;
    let aaPadding = 1.0;
    let totalHalf = halfWidth + aaPadding;
    let perpNDC = vec2(-normDir.y, normDir.x) * totalHalf * 2.0 / scene.viewport;

    var pos: vec2<f32>;
    var t: f32;
    var edge: f32;
    switch vid {
        case 0u: { pos = startNDC - perpNDC; t = 0.0; edge = -1.0; }
        case 1u: { pos = startNDC + perpNDC; t = 0.0; edge = 1.0; }
        case 2u: { pos = endNDC + perpNDC; t = 1.0; edge = 1.0; }
        case 3u: { pos = startNDC - perpNDC; t = 0.0; edge = -1.0; }
        case 4u: { pos = endNDC + perpNDC; t = 1.0; edge = 1.0; }
        case 5u: { pos = endNDC - perpNDC; t = 1.0; edge = -1.0; }
        default: { pos = startNDC; t = 0.0; edge = 0.0; }
    }

    let depth = mix(startClip.z / startClip.w, endClip.z / endClip.w, t);

    // edge is -1 to +1 across quad, convert to pixel distance from line center
    let pixelDist = edge * totalHalf;

    var out: VertexOutput;
    out.position = vec4(pos, depth, 1.0);
    out.color = vec4(line.color.rgb, line.color.a * line.opacity);
    out.dist = pixelDist;
    out.halfWidth = halfWidth;
    return out;
}

struct FragmentOutput {
    @location(0) color: vec4<f32>,
    @location(1) mask: f32,
}

@fragment
fn fs(input: VertexOutput) -> FragmentOutput {
    let dist = abs(input.dist);
    let aa = 1.0 - smoothstep(input.halfWidth - 0.5, input.halfWidth + 0.5, dist);
    var out: FragmentOutput;
    out.color = vec4(input.color.rgb, input.color.a * aa);
    out.mask = select(0.0, 1.0, aa > 0.01);
    return out;
}
`;

export function createLinesPipeline(
    device: GPUDevice,
    format: GPUTextureFormat,
    maskFormat: GPUTextureFormat
): GPURenderPipeline {
    const module = device.createShaderModule({ code: lineShader });

    return device.createRenderPipeline({
        layout: "auto",
        vertex: {
            module,
            entryPoint: "vs",
        },
        fragment: {
            module,
            entryPoint: "fs",
            targets: [
                {
                    format,
                    blend: {
                        color: {
                            srcFactor: "src-alpha",
                            dstFactor: "one-minus-src-alpha",
                            operation: "add",
                        },
                        alpha: {
                            srcFactor: "one",
                            dstFactor: "one-minus-src-alpha",
                            operation: "add",
                        },
                    },
                },
                {
                    format: maskFormat,
                    writeMask: GPUColorWrite.RED,
                },
            ],
        },
        primitive: {
            topology: "triangle-list",
        },
        depthStencil: {
            format: DEPTH_FORMAT,
            depthCompare: "less",
            depthWriteEnabled: false,
        },
    });
}

function createLinesContributor(config: LinesConfig): DrawContributor {
    let pipeline: GPURenderPipeline | null = null;
    let bindGroup: GPUBindGroup | null = null;

    return {
        id: "lines",
        order: 0,

        draw(pass: GPURenderPassEncoder, ctx: DrawContext) {
            const count = config.getCount();
            if (count === 0) return;

            if (!pipeline) {
                pipeline = createLinesPipeline(ctx.device, ctx.format, ctx.maskFormat);
            }

            if (!bindGroup) {
                bindGroup = ctx.device.createBindGroup({
                    layout: pipeline.getBindGroupLayout(0),
                    entries: [
                        { binding: 0, resource: { buffer: config.scene } },
                        { binding: 1, resource: { buffer: config.entityIds } },
                        { binding: 2, resource: { buffer: config.lines } },
                        { binding: 3, resource: { buffer: config.matrices } },
                    ],
                });
            }

            pass.setPipeline(pipeline);
            pass.setBindGroup(0, bindGroup);
            pass.draw(6, count);
        },
    };
}

export interface LinesState {
    buffer: GPUBuffer;
    entityIds: GPUBuffer;
    count: number;
}

export const Lines = resource<LinesState>("lines");

const entityIdArray = new Uint32Array(MAX_ENTITIES);

const LinesSystem: System = {
    group: "draw",

    update(state: State) {
        const compute = Compute.from(state);
        const lines = Lines.from(state);
        if (!compute || !lines) return;

        const { device } = compute;

        let count = 0;
        for (const eid of state.query([Line, Transform])) {
            if (!Line.visible[eid]) continue;
            entityIdArray[count++] = eid;
        }

        device.queue.writeBuffer(lines.buffer, 0, LineData.data);
        device.queue.writeBuffer(lines.entityIds, 0, entityIdArray, 0, count);
        lines.count = count;
    },
};

export const LinesPlugin: Plugin = {
    systems: [LinesSystem],
    components: { Line },
    dependencies: [ComputePlugin, RenderPlugin],

    initialize(state: State) {
        const compute = Compute.from(state);
        const render = Render.from(state);
        if (!compute || !render) return;

        const { device } = compute;

        const linesState: LinesState = {
            buffer: device.createBuffer({
                label: "lines",
                size: MAX_ENTITIES * 12 * 4,
                usage: GPUBufferUsage.STORAGE | GPUBufferUsage.COPY_DST,
            }),
            entityIds: createEntityIdBuffer(device, MAX_ENTITIES),
            count: 0,
        };

        state.setResource(Lines, linesState);

        registerDrawContributor(
            state,
            createLinesContributor({
                scene: render.scene,
                lines: linesState.buffer,
                entityIds: linesState.entityIds,
                matrices: render.matrices,
                getCount: () => linesState.count,
            })
        );
    },
};
