import { setTraits } from "../../core/component";
import { WorldTransform } from "../transforms";
import { clearColor } from "./forward";
import { perspective, orthographic, multiply, invert } from "./scene";

export const RenderMode = {
    Raster: 0,
    Raytracing: 1,
} as const;

export const DebugMode = {
    Color: 0,
    Depth: 1,
    Normal: 2,
    Material: 3,
    Hit: 4,
} as const;

export const CameraMode = {
    Perspective: 0,
    Orthographic: 1,
} as const;

export const Camera = {
    fov: [] as number[],
    near: [] as number[],
    far: [] as number[],
    active: [] as number[],
    clearColor: [] as number[],
    renderMode: [] as number[],
    debugMode: [] as number[],
    mode: [] as number[],
    size: [] as number[],
};

setTraits(Camera, {
    defaults: () => ({
        fov: 60,
        near: 0.1,
        far: 1000,
        active: 1,
        clearColor: 0x1a1a1a,
        renderMode: RenderMode.Raster,
        debugMode: DebugMode.Color,
        mode: CameraMode.Perspective,
        size: 5,
    }),
});

export const Tonemap = {
    exposure: [] as number[],
};

setTraits(Tonemap, {
    defaults: () => ({ exposure: 1.0 }),
});

export const FXAA = {};

export const Vignette = {
    strength: [] as number[],
    inner: [] as number[],
    outer: [] as number[],
};

setTraits(Vignette, {
    defaults: () => ({ strength: 0.5, inner: 0.4, outer: 0.8 }),
});

export function unpackColor(packed: number): { r: number; g: number; b: number } {
    return {
        r: ((packed >> 16) & 0xff) / 255,
        g: ((packed >> 8) & 0xff) / 255,
        b: (packed & 0xff) / 255,
    };
}

export function uploadCamera(
    device: GPUDevice,
    buffer: GPUBuffer,
    eid: number,
    width: number,
    height: number
): void {
    const color = unpackColor(Camera.clearColor[eid]);
    clearColor.r = color.r;
    clearColor.g = color.g;
    clearColor.b = color.b;

    const aspect = width / height;
    const proj =
        Camera.mode[eid] === CameraMode.Orthographic
            ? orthographic(Camera.size[eid], aspect, Camera.near[eid], Camera.far[eid])
            : perspective(Camera.fov[eid], aspect, Camera.near[eid], Camera.far[eid]);
    const world = WorldTransform.data.subarray(eid * 16, eid * 16 + 16);
    const view = invert(world);
    const viewProj = multiply(proj, view);

    device.queue.writeBuffer(buffer, 0, viewProj as Float32Array<ArrayBuffer>);
    device.queue.writeBuffer(buffer, 64, world as Float32Array<ArrayBuffer>);
    device.queue.writeBuffer(
        buffer,
        176,
        new Float32Array([Camera.mode[eid], Camera.size[eid], width, height])
    );
}
