import {
    resource,
    type State,
    type System,
    type Plugin,
    type InputState,
    type MouseState,
} from "../../core";

export const Input = resource<InputState>("input");

const keys = new Set<string>();
const keysPressed = new Set<string>();
const keysReleased = new Set<string>();

const mouse: MouseState = {
    deltaX: 0,
    deltaY: 0,
    scrollDelta: 0,
    left: false,
    right: false,
    middle: false,
};

const inputState: InputState = {
    mouse,
    isKeyDown: (code: string) => keys.has(code),
    isKeyPressed: (code: string) => keysPressed.has(code),
    isKeyReleased: (code: string) => keysReleased.has(code),
};

let canvas: HTMLCanvasElement | null = null;
let lastPointerX = 0;
let lastPointerY = 0;
let activePointerId: number | null = null;
let activeButton: number | null = null;

function handleKeyDown(e: KeyboardEvent): void {
    if (!keys.has(e.code)) {
        keysPressed.add(e.code);
    }
    keys.add(e.code);
}

function handleKeyUp(e: KeyboardEvent): void {
    keys.delete(e.code);
    keysReleased.add(e.code);
}

function setButtonState(button: number, pressed: boolean): void {
    if (button === 0) mouse.left = pressed;
    if (button === 1) mouse.middle = pressed;
    if (button === 2) mouse.right = pressed;
}

function clearPointerState(): void {
    if (activeButton !== null) {
        setButtonState(activeButton, false);
    }
    activePointerId = null;
    activeButton = null;
    lastPointerX = 0;
    lastPointerY = 0;
}

function handlePointerDown(e: PointerEvent): void {
    if (e.target !== canvas) return;
    if (activePointerId === null) {
        activePointerId = e.pointerId;
        activeButton = e.button;
        lastPointerX = e.clientX;
        lastPointerY = e.clientY;
        setButtonState(e.button, true);
        canvas!.setPointerCapture(e.pointerId);
        e.preventDefault();
    }
}

function handlePointerUp(e: PointerEvent): void {
    if (e.pointerId === activePointerId) {
        canvas?.releasePointerCapture(e.pointerId);
        clearPointerState();
    }
}

function handlePointerCancel(e: PointerEvent): void {
    if (e.pointerId === activePointerId) {
        clearPointerState();
    }
}

function handlePointerMove(e: PointerEvent): void {
    if (e.pointerId !== activePointerId) return;
    e.preventDefault();
    mouse.deltaX += e.clientX - lastPointerX;
    mouse.deltaY += e.clientY - lastPointerY;
    lastPointerX = e.clientX;
    lastPointerY = e.clientY;
}

function handleWheel(e: WheelEvent): void {
    if (e.target !== canvas) return;
    mouse.scrollDelta += e.deltaY;
    e.preventDefault();
}

function handleContextMenu(e: Event): void {
    if (e.target === canvas) {
        e.preventDefault();
    }
}

function resetFrameState(): void {
    keysPressed.clear();
    keysReleased.clear();
    mouse.deltaX = 0;
    mouse.deltaY = 0;
    mouse.scrollDelta = 0;
}

function clearAllState(): void {
    keys.clear();
    keysPressed.clear();
    keysReleased.clear();
    mouse.deltaX = 0;
    mouse.deltaY = 0;
    mouse.scrollDelta = 0;
    mouse.left = false;
    mouse.right = false;
    mouse.middle = false;
    activePointerId = null;
    activeButton = null;
    lastPointerX = 0;
    lastPointerY = 0;
}

export const InputSystem: System = {
    group: "simulation",

    setup(state: State) {
        if (!state.canvas) return;
        canvas = state.canvas;
        canvas.style.touchAction = "none";

        window.addEventListener("keydown", handleKeyDown);
        window.addEventListener("keyup", handleKeyUp);
        canvas.addEventListener("pointerdown", handlePointerDown);
        window.addEventListener("pointerup", handlePointerUp);
        window.addEventListener("pointercancel", handlePointerCancel);
        window.addEventListener("pointermove", handlePointerMove);
        canvas.addEventListener("wheel", handleWheel, { passive: false });
        canvas.addEventListener("contextmenu", handleContextMenu);

        state.setResource(Input, inputState);
    },

    dispose(state: State) {
        if (canvas) {
            window.removeEventListener("keydown", handleKeyDown);
            window.removeEventListener("keyup", handleKeyUp);
            canvas.removeEventListener("pointerdown", handlePointerDown);
            window.removeEventListener("pointerup", handlePointerUp);
            window.removeEventListener("pointercancel", handlePointerCancel);
            window.removeEventListener("pointermove", handlePointerMove);
            canvas.removeEventListener("wheel", handleWheel);
            canvas.removeEventListener("contextmenu", handleContextMenu);
            canvas = null;
        }

        clearAllState();
        state.deleteResource(Input);
    },
};

const InputResetSystem: System = {
    group: "draw",
    last: true,

    update() {
        resetFrameState();
    },
};

export const InputPlugin: Plugin = {
    systems: [InputSystem, InputResetSystem],
};
