import TinySDF from "@mapbox/tiny-sdf";
import {
    MAX_ENTITIES,
    resource,
    registerPostLoadHook,
    type Plugin,
    type State,
    type System,
    type PostLoadContext,
} from "../../core";
import { setTraits, type FieldAccessor } from "../../core/component";
import { Compute, ComputePlugin } from "../../standard/compute";
import {
    Render,
    RenderPlugin,
    DEPTH_FORMAT,
    registerDrawContributor,
    type DrawContributor,
    type DrawContext,
} from "../../standard/render";
import { Transform } from "../../standard/transforms";

const MAX_GLYPHS = 50000;
const GLYPH_FLOATS = 16;
const SDF_EXPONENT = 9;
const SDF_CUTOFF = 0.5;

let customFontFamily: string | null = null;
let customFontWeight: string = "normal";

function encodeExponentialSdf(linearData: Uint8Array): Uint8Array {
    const encoded = new Uint8Array(linearData.length);
    for (let i = 0; i < linearData.length; i++) {
        // TinySDF outputs: 0=outside, 255=inside, cutoff*255=edge
        // Convert to signed distance: positive=outside, negative=inside
        const raw = linearData[i] / 255;
        const signedDist = (SDF_CUTOFF - raw) / SDF_CUTOFF;

        // Apply exponential encoding (Troika formula)
        const absDist = Math.min(1, Math.abs(signedDist));
        let alpha = Math.pow(1 - absDist, SDF_EXPONENT) / 2;
        if (signedDist < 0) {
            alpha = 1 - alpha;
        }
        encoded[i] = Math.round(Math.max(0, Math.min(255, alpha * 255)));
    }
    return encoded;
}

export async function setTextFont(fontFamily: string, weight: number = 400): Promise<void> {
    const fontSpec = `${weight} 128px "${fontFamily}"`;
    await document.fonts.load(fontSpec);
    customFontFamily = fontFamily;
    customFontWeight = weight >= 600 ? "bold" : "normal";
}

export const TextData = {
    data: new Float32Array(MAX_ENTITIES * 12),
};

interface TextProxy extends Array<number>, FieldAccessor {}

function textProxy(offset: number): TextProxy {
    const data = TextData.data;

    function getValue(eid: number): number {
        return data[eid * 12 + offset];
    }

    function setValue(eid: number, value: number): void {
        data[eid * 12 + offset] = value;
    }

    return new Proxy([] as unknown as TextProxy, {
        get(_, prop) {
            if (prop === "get") return getValue;
            if (prop === "set") return setValue;
            const eid = Number(prop);
            if (Number.isNaN(eid)) return undefined;
            return getValue(eid);
        },
        set(_, prop, value) {
            const eid = Number(prop);
            if (Number.isNaN(eid)) return false;
            setValue(eid, value);
            return true;
        },
    });
}

function colorProxy(): TextProxy {
    const data = TextData.data;

    function getValue(eid: number): number {
        const offset = eid * 12 + 8;
        const r = Math.round(data[offset] * 255);
        const g = Math.round(data[offset + 1] * 255);
        const b = Math.round(data[offset + 2] * 255);
        return (r << 16) | (g << 8) | b;
    }

    function setValue(eid: number, value: number): void {
        const offset = eid * 12 + 8;
        data[offset] = ((value >> 16) & 0xff) / 255;
        data[offset + 1] = ((value >> 8) & 0xff) / 255;
        data[offset + 2] = (value & 0xff) / 255;
        data[offset + 3] = 1;
    }

    return new Proxy([] as unknown as TextProxy, {
        get(_, prop) {
            if (prop === "get") return getValue;
            if (prop === "set") return setValue;
            const eid = Number(prop);
            if (Number.isNaN(eid)) return undefined;
            return getValue(eid);
        },
        set(_, prop, value) {
            const eid = Number(prop);
            if (Number.isNaN(eid)) return false;
            setValue(eid, value);
            return true;
        },
    });
}

function colorChannelProxy(channelIndex: number): TextProxy {
    const data = TextData.data;

    function getValue(eid: number): number {
        return data[eid * 12 + 8 + channelIndex];
    }

    function setValue(eid: number, value: number): void {
        data[eid * 12 + 8 + channelIndex] = value;
    }

    return new Proxy([] as unknown as TextProxy, {
        get(_, prop) {
            if (prop === "get") return getValue;
            if (prop === "set") return setValue;
            const eid = Number(prop);
            if (Number.isNaN(eid)) return undefined;
            return getValue(eid);
        },
        set(_, prop, value) {
            const eid = Number(prop);
            if (Number.isNaN(eid)) return false;
            setValue(eid, value);
            return true;
        },
    });
}

const textContent = new Map<number, string>();

interface TextContentProxy {
    [eid: number]: string | undefined;
}

function contentProxy(): TextContentProxy {
    return new Proxy({} as TextContentProxy, {
        get(_, prop) {
            const eid = Number(prop);
            if (Number.isNaN(eid)) return undefined;
            return textContent.get(eid);
        },
        set(_, prop, value) {
            const eid = Number(prop);
            if (Number.isNaN(eid)) return false;
            if (value === undefined || value === null) {
                textContent.delete(eid);
            } else {
                textContent.set(eid, value);
            }
            return true;
        },
    });
}

export const Text = {
    content: contentProxy(),
    fontSize: textProxy(0),
    opacity: textProxy(1),
    visible: textProxy(2),
    anchorX: textProxy(3),
    anchorY: textProxy(4),
    color: colorProxy(),
    colorR: colorChannelProxy(0),
    colorG: colorChannelProxy(1),
    colorB: colorChannelProxy(2),
};

interface PendingText {
    readonly eid: number;
    readonly content: string;
}

let pendingTextContent: PendingText[] = [];

function parseTextAttrs(attrs: Record<string, string>): Record<string, string> {
    if (attrs._value) {
        const parsed: Record<string, string> = {};
        for (const part of attrs._value.split(";")) {
            const colonIdx = part.indexOf(":");
            if (colonIdx === -1) continue;
            const key = part.slice(0, colonIdx).trim();
            const value = part.slice(colonIdx + 1).trim();
            if (key && value) parsed[key] = value;
        }
        return parsed;
    }
    return attrs;
}

function finalizePendingText(_state: State, _context: PostLoadContext): void {
    for (const pending of pendingTextContent) {
        Text.content[pending.eid] = pending.content;
    }
    pendingTextContent = [];
}

setTraits(Text, {
    defaults: () => ({
        fontSize: 1,
        opacity: 1,
        visible: 1,
        anchorX: 0,
        anchorY: 0,
        color: 0xffffff,
    }),
    adapter: (attrs: Record<string, string>, eid: number) => {
        const parsed = parseTextAttrs(attrs);
        const result: Record<string, number> = {};

        if (parsed.content) {
            pendingTextContent.push({ eid, content: parsed.content });
        }

        if (parsed["font-size"]) result.fontSize = parseFloat(parsed["font-size"]);
        if (parsed.fontSize) result.fontSize = parseFloat(parsed.fontSize);
        if (parsed.opacity) result.opacity = parseFloat(parsed.opacity);
        if (parsed.visible) result.visible = parseFloat(parsed.visible);
        if (parsed["anchor-x"]) result.anchorX = parseFloat(parsed["anchor-x"]);
        if (parsed.anchorX) result.anchorX = parseFloat(parsed.anchorX);
        if (parsed["anchor-y"]) result.anchorY = parseFloat(parsed["anchor-y"]);
        if (parsed.anchorY) result.anchorY = parseFloat(parsed.anchorY);
        if (parsed.color) {
            const colorStr = parsed.color;
            if (colorStr.startsWith("0x") || colorStr.startsWith("0X")) {
                result.color = parseInt(colorStr, 16);
            } else if (colorStr.startsWith("#")) {
                result.color = parseInt(colorStr.slice(1), 16);
            } else {
                result.color = parseInt(colorStr, 10);
            }
        }

        return result;
    },
    accessors: {
        fontSize: Text.fontSize,
        opacity: Text.opacity,
        visible: Text.visible,
        anchorX: Text.anchorX,
        anchorY: Text.anchorY,
        color: Text.color,
        colorR: Text.colorR,
        colorG: Text.colorG,
        colorB: Text.colorB,
    },
});

interface GlyphMetrics {
    width: number;
    height: number;
    glyphWidth: number;
    glyphHeight: number;
    glyphTop: number;
    glyphLeft: number;
    advance: number;
    u0: number;
    v0: number;
    u1: number;
    v1: number;
}

interface GlyphAtlas {
    texture: GPUTexture;
    textureView: GPUTextureView;
    width: number;
    height: number;
    glyphs: Map<string, GlyphMetrics>;
    rowHeight: number;
    cursorX: number;
    cursorY: number;
    sdf: TinySDF;
    sdfFontSize: number;
}

function createGlyphAtlas(device: GPUDevice): GlyphAtlas {
    const width = 2048;
    const height = 2048;
    const fontSize = 128;

    const texture = device.createTexture({
        size: { width, height },
        format: "r8unorm",
        usage: GPUTextureUsage.TEXTURE_BINDING | GPUTextureUsage.COPY_DST,
        label: "glyphAtlas",
    });

    const sdf = new TinySDF({
        fontSize,
        fontFamily: customFontFamily ?? "system-ui, sans-serif",
        fontWeight: customFontWeight,
        fontStyle: "normal",
        buffer: 16,
        radius: 48,
        cutoff: 0.5,
    });

    return {
        texture,
        textureView: texture.createView(),
        width,
        height,
        glyphs: new Map(),
        rowHeight: 0,
        cursorX: 0,
        cursorY: 0,
        sdf,
        sdfFontSize: fontSize,
    };
}

function ensureGlyph(device: GPUDevice, atlas: GlyphAtlas, char: string): GlyphMetrics {
    const existing = atlas.glyphs.get(char);
    if (existing) return existing;

    const glyph = atlas.sdf.draw(char);

    if (atlas.cursorX + glyph.width > atlas.width) {
        atlas.cursorX = 0;
        atlas.cursorY += atlas.rowHeight;
        atlas.rowHeight = 0;
    }

    if (atlas.cursorY + glyph.height > atlas.height) {
        throw new Error("Glyph atlas full");
    }

    const glyphData = encodeExponentialSdf(new Uint8Array(glyph.data));

    device.queue.writeTexture(
        {
            texture: atlas.texture,
            origin: { x: atlas.cursorX, y: atlas.cursorY },
        },
        glyphData as Uint8Array<ArrayBuffer>,
        { bytesPerRow: glyph.width },
        { width: glyph.width, height: glyph.height }
    );

    const metrics: GlyphMetrics = {
        width: glyph.width,
        height: glyph.height,
        glyphWidth: glyph.glyphWidth,
        glyphHeight: glyph.glyphHeight,
        glyphTop: glyph.glyphTop,
        glyphLeft: glyph.glyphLeft,
        advance: glyph.glyphAdvance,
        u0: atlas.cursorX / atlas.width,
        v0: atlas.cursorY / atlas.height,
        u1: (atlas.cursorX + glyph.width) / atlas.width,
        v1: (atlas.cursorY + glyph.height) / atlas.height,
    };

    atlas.glyphs.set(char, metrics);
    atlas.cursorX += glyph.width;
    atlas.rowHeight = Math.max(atlas.rowHeight, glyph.height);

    return metrics;
}

function ensureString(device: GPUDevice, atlas: GlyphAtlas, text: string): void {
    for (const char of text) {
        ensureGlyph(device, atlas, char);
    }
}

interface LayoutGlyph {
    x: number;
    y: number;
    width: number;
    height: number;
    texelWidth: number;
    texelHeight: number;
    u0: number;
    v0: number;
    u1: number;
    v1: number;
}

interface LayoutResult {
    glyphs: LayoutGlyph[];
    width: number;
    height: number;
}

function layoutText(text: string, atlas: GlyphAtlas, fontSize: number): LayoutResult {
    const glyphs: LayoutGlyph[] = [];
    const scale = fontSize / atlas.sdfFontSize;

    let cursorX = 0;
    let maxHeight = 0;

    for (const char of text) {
        const metrics = atlas.glyphs.get(char);
        if (!metrics) continue;

        const glyphW = metrics.glyphWidth * scale;
        const glyphH = metrics.glyphHeight * scale;
        const advance = metrics.advance * scale;

        const x = cursorX + metrics.glyphLeft * scale;
        const y = (metrics.glyphTop - metrics.glyphHeight) * scale;

        glyphs.push({
            x,
            y,
            width: glyphW,
            height: glyphH,
            texelWidth: metrics.width,
            texelHeight: metrics.height,
            u0: metrics.u0,
            v0: metrics.v0,
            u1: metrics.u1,
            v1: metrics.v1,
        });

        cursorX += advance;
        maxHeight = Math.max(maxHeight, glyphH);
    }

    return {
        glyphs,
        width: cursorX,
        height: maxHeight,
    };
}

const textShader = /* wgsl */ `
struct Scene {
    viewProj: mat4x4<f32>,
    cameraWorld: mat4x4<f32>,
    ambientColor: vec4<f32>,
    sunDirection: vec4<f32>,
    sunColor: vec4<f32>,
    cameraMode: f32,
    cameraSize: f32,
    viewport: vec2<f32>,
}

struct GlyphInstance {
    posX: f32,
    posY: f32,
    posZ: f32,
    entityId: u32,
    width: f32,
    height: f32,
    texelWidth: f32,
    texelHeight: f32,
    u0: f32,
    v0: f32,
    u1: f32,
    v1: f32,
    color: vec4<f32>,
}

@group(0) @binding(0) var<uniform> scene: Scene;
@group(0) @binding(1) var<storage, read> glyphs: array<GlyphInstance>;
@group(0) @binding(2) var atlasTexture: texture_2d<f32>;
@group(0) @binding(3) var atlasSampler: sampler;
@group(0) @binding(4) var<storage, read> matrices: array<mat4x4<f32>>;

struct VertexOutput {
    @builtin(position) position: vec4<f32>,
    @location(0) uv: vec2<f32>,
    @location(1) color: vec4<f32>,
    @location(2) localUV: vec2<f32>,
    @location(3) texelSize: vec2<f32>,
}

@vertex
fn vs(@builtin(vertex_index) vid: u32) -> VertexOutput {
    let glyphIdx = vid / 6u;
    let cornerIdx = vid % 6u;

    let glyph = glyphs[glyphIdx];

    var localPos: vec2<f32>;
    var uv: vec2<f32>;

    switch cornerIdx {
        case 0u: {
            localPos = vec2(0.0, 0.0);
            uv = vec2(glyph.u0, glyph.v1);
        }
        case 1u: {
            localPos = vec2(1.0, 0.0);
            uv = vec2(glyph.u1, glyph.v1);
        }
        case 2u: {
            localPos = vec2(1.0, 1.0);
            uv = vec2(glyph.u1, glyph.v0);
        }
        case 3u: {
            localPos = vec2(0.0, 0.0);
            uv = vec2(glyph.u0, glyph.v1);
        }
        case 4u: {
            localPos = vec2(1.0, 1.0);
            uv = vec2(glyph.u1, glyph.v0);
        }
        case 5u: {
            localPos = vec2(0.0, 1.0);
            uv = vec2(glyph.u0, glyph.v0);
        }
        default: {
            localPos = vec2(0.0);
            uv = vec2(0.0);
        }
    }

    let localPos3 = vec3(
        glyph.posX + localPos.x * glyph.width,
        glyph.posY + localPos.y * glyph.height,
        glyph.posZ
    );

    let transform = matrices[glyph.entityId];
    let worldPos = transform * vec4(localPos3, 1.0);

    var out: VertexOutput;
    out.position = scene.viewProj * worldPos;
    out.uv = uv;
    out.color = glyph.color;
    out.localUV = localPos;
    out.texelSize = vec2(glyph.texelWidth, glyph.texelHeight);
    return out;
}

struct FragmentOutput {
    @location(0) color: vec4<f32>,
    @location(1) mask: f32,
}

@fragment
fn fs(input: VertexOutput) -> FragmentOutput {
    let sdfValue = textureSample(atlasTexture, atlasSampler, input.uv).r;

    // Decode exponential SDF (inverse of Troika-style encoding)
    let sdfExponent = 9.0;
    let isOutside = sdfValue < 0.5;
    let processedAlpha = select(1.0 - sdfValue, sdfValue, isOutside);
    let normalizedDist = 1.0 - pow(2.0 * processedAlpha, 1.0 / sdfExponent);
    let signedDist = select(-normalizedDist, normalizedDist, isOutside);

    // Troika-style AA using screen-space derivatives
    let texelFootprint = fwidth(input.localUV * input.texelSize);
    let aaRadius = length(texelFootprint) * 0.5;
    let smoothing = clamp(aaRadius * 0.5, 0.01, 0.25);

    // Edge at signedDist = 0, positive offset = thicker text
    let edgeOffset = 0.02;
    let alpha = smoothstep(edgeOffset + smoothing, edgeOffset - smoothing, signedDist);

    if alpha < 0.01 {
        discard;
    }

    var out: FragmentOutput;
    out.color = vec4(input.color.rgb, input.color.a * alpha);
    out.mask = select(0.0, 1.0, alpha > 0.01);
    return out;
}
`;

function createTextPipeline(
    device: GPUDevice,
    format: GPUTextureFormat,
    maskFormat: GPUTextureFormat
): GPURenderPipeline {
    const module = device.createShaderModule({ code: textShader });

    return device.createRenderPipeline({
        layout: "auto",
        vertex: {
            module,
            entryPoint: "vs",
        },
        fragment: {
            module,
            entryPoint: "fs",
            targets: [
                {
                    format,
                    blend: {
                        color: {
                            srcFactor: "src-alpha",
                            dstFactor: "one-minus-src-alpha",
                            operation: "add",
                        },
                        alpha: {
                            srcFactor: "one",
                            dstFactor: "one-minus-src-alpha",
                            operation: "add",
                        },
                    },
                },
                {
                    format: maskFormat,
                    writeMask: GPUColorWrite.RED,
                },
            ],
        },
        primitive: {
            topology: "triangle-list",
            cullMode: "none",
        },
        depthStencil: {
            format: DEPTH_FORMAT,
            depthCompare: "less",
            depthWriteEnabled: false,
        },
    });
}

export interface TextConfig {
    scene: GPUBuffer;
    glyphs: GPUBuffer;
    atlas: GPUTextureView;
    sampler: GPUSampler;
    matrices: GPUBuffer;
    getCount: () => number;
}

function createTextContributor(config: TextConfig): DrawContributor {
    let pipeline: GPURenderPipeline | null = null;
    let bindGroup: GPUBindGroup | null = null;

    return {
        id: "text",
        order: 2,

        draw(pass: GPURenderPassEncoder, ctx: DrawContext) {
            const count = config.getCount();
            if (count === 0) return;

            if (!pipeline) {
                pipeline = createTextPipeline(ctx.device, ctx.format, ctx.maskFormat);
            }

            if (!bindGroup) {
                bindGroup = ctx.device.createBindGroup({
                    layout: pipeline.getBindGroupLayout(0),
                    entries: [
                        { binding: 0, resource: { buffer: config.scene } },
                        { binding: 1, resource: { buffer: config.glyphs } },
                        { binding: 2, resource: config.atlas },
                        { binding: 3, resource: config.sampler },
                        { binding: 4, resource: { buffer: config.matrices } },
                    ],
                });
            }

            pass.setPipeline(pipeline);
            pass.setBindGroup(0, bindGroup);
            pass.draw(count * 6);
        },
    };
}

export interface TextState {
    atlas: GlyphAtlas;
    sampler: GPUSampler;
    buffer: GPUBuffer;
    staging: Float32Array;
    count: number;
}

export const TextResource = resource<TextState>("text");

const TextSystem: System = {
    group: "draw",

    update(state: State) {
        const compute = Compute.from(state);
        const text = TextResource.from(state);
        if (!compute || !text) return;

        const { device } = compute;
        const { atlas, staging } = text;
        const stagingU32 = new Uint32Array(staging.buffer);

        let glyphCount = 0;

        for (const eid of state.query([Text, Transform])) {
            if (!Text.visible[eid]) continue;

            const content = textContent.get(eid);
            if (!content) continue;

            ensureString(device, atlas, content);

            const fontSize = Text.fontSize[eid];
            const layout = layoutText(content, atlas, fontSize);

            const anchorX = Text.anchorX[eid];
            const anchorY = Text.anchorY[eid];
            const offsetX = -layout.width * anchorX;
            const offsetY = -layout.height * anchorY;

            const color = Text.color[eid];
            const r = ((color >> 16) & 0xff) / 255;
            const g = ((color >> 8) & 0xff) / 255;
            const b = (color & 0xff) / 255;
            const a = Text.opacity[eid];

            for (const glyph of layout.glyphs) {
                if (glyphCount >= MAX_GLYPHS) break;

                const offset = glyphCount * GLYPH_FLOATS;

                staging[offset + 0] = offsetX + glyph.x;
                staging[offset + 1] = offsetY + glyph.y;
                staging[offset + 2] = 0;
                stagingU32[offset + 3] = eid;

                staging[offset + 4] = glyph.width;
                staging[offset + 5] = glyph.height;
                staging[offset + 6] = glyph.texelWidth;
                staging[offset + 7] = glyph.texelHeight;

                staging[offset + 8] = glyph.u0;
                staging[offset + 9] = glyph.v0;
                staging[offset + 10] = glyph.u1;
                staging[offset + 11] = glyph.v1;

                staging[offset + 12] = r;
                staging[offset + 13] = g;
                staging[offset + 14] = b;
                staging[offset + 15] = a;

                glyphCount++;
            }
        }

        text.count = glyphCount;

        if (glyphCount > 0) {
            device.queue.writeBuffer(
                text.buffer,
                0,
                staging.buffer,
                0,
                glyphCount * GLYPH_FLOATS * 4
            );
        }
    },
};

export const TextPlugin: Plugin = {
    systems: [TextSystem],
    components: { Text },
    dependencies: [ComputePlugin, RenderPlugin],

    initialize(state: State) {
        registerPostLoadHook(finalizePendingText);

        const compute = Compute.from(state);
        const render = Render.from(state);
        if (!compute || !render) return;

        const { device } = compute;

        const atlas = createGlyphAtlas(device);
        const sampler = device.createSampler({
            magFilter: "linear",
            minFilter: "linear",
        });

        const textState: TextState = {
            atlas,
            sampler,
            buffer: device.createBuffer({
                label: "glyphs",
                size: MAX_GLYPHS * GLYPH_FLOATS * 4,
                usage: GPUBufferUsage.STORAGE | GPUBufferUsage.COPY_DST,
            }),
            staging: new Float32Array(MAX_GLYPHS * GLYPH_FLOATS),
            count: 0,
        };

        state.setResource(TextResource, textState);

        registerDrawContributor(
            state,
            createTextContributor({
                scene: render.scene,
                glyphs: textState.buffer,
                atlas: atlas.textureView,
                sampler,
                matrices: render.matrices,
                getCount: () => textState.count,
            })
        );
    },
};
