const DEG_TO_RAD = Math.PI / 180;
const RAD_TO_DEG = 180 / Math.PI;

export function clamp(value: number, min: number, max: number): number {
    return value < min ? min : value > max ? max : value;
}

export function lerp(a: number, b: number, t: number): number {
    return a + (b - a) * t;
}

export function slerp(
    fromX: number,
    fromY: number,
    fromZ: number,
    fromW: number,
    toX: number,
    toY: number,
    toZ: number,
    toW: number,
    t: number
): { x: number; y: number; z: number; w: number } {
    if (!Number.isFinite(t) || !Number.isFinite(fromW) || !Number.isFinite(toW)) {
        throw new Error(
            `slerp received NaN: from=[${fromX},${fromY},${fromZ},${fromW}], to=[${toX},${toY},${toZ},${toW}], t=${t}`
        );
    }

    let dot = fromX * toX + fromY * toY + fromZ * toZ + fromW * toW;

    if (dot < 0) {
        dot = -dot;
        toX = -toX;
        toY = -toY;
        toZ = -toZ;
        toW = -toW;
    }

    let s0: number, s1: number;
    if (dot > 0.9995) {
        s0 = 1 - t;
        s1 = t;
    } else {
        const theta0 = Math.acos(dot);
        const sinTheta0 = Math.sqrt(1 - dot * dot);
        const theta = theta0 * t;
        const sinTheta = Math.sin(theta);
        s0 = Math.cos(theta) - (dot * sinTheta) / sinTheta0;
        s1 = sinTheta / sinTheta0;
    }

    return {
        x: s0 * fromX + s1 * toX,
        y: s0 * fromY + s1 * toY,
        z: s0 * fromZ + s1 * toZ,
        w: s0 * fromW + s1 * toW,
    };
}

export function rotateQuaternion(
    qx: number,
    qy: number,
    qz: number,
    qw: number,
    dx: number,
    dy: number,
    dz: number
): { x: number; y: number; z: number; w: number } {
    const hx = dx * DEG_TO_RAD * 0.5;
    const hy = dy * DEG_TO_RAD * 0.5;
    const hz = dz * DEG_TO_RAD * 0.5;
    const cx = Math.cos(hx),
        sx = Math.sin(hx);
    const cy = Math.cos(hy),
        sy = Math.sin(hy);
    const cz = Math.cos(hz),
        sz = Math.sin(hz);

    const bx = sx * cy * cz + cx * sy * sz;
    const by = cx * sy * cz - sx * cy * sz;
    const bz = cx * cy * sz + sx * sy * cz;
    const bw = cx * cy * cz - sx * sy * sz;

    return {
        x: qw * bx + qx * bw + qy * bz - qz * by,
        y: qw * by - qx * bz + qy * bw + qz * bx,
        z: qw * bz + qx * by - qy * bx + qz * bw,
        w: qw * bw - qx * bx - qy * by - qz * bz,
    };
}

export function eulerToQuaternion(
    x: number,
    y: number,
    z: number
): { x: number; y: number; z: number; w: number } {
    const hx = x * DEG_TO_RAD * 0.5;
    const hy = y * DEG_TO_RAD * 0.5;
    const hz = z * DEG_TO_RAD * 0.5;
    const cx = Math.cos(hx),
        sx = Math.sin(hx);
    const cy = Math.cos(hy),
        sy = Math.sin(hy);
    const cz = Math.cos(hz),
        sz = Math.sin(hz);

    return {
        x: sx * cy * cz + cx * sy * sz,
        y: cx * sy * cz - sx * cy * sz,
        z: cx * cy * sz + sx * sy * cz,
        w: cx * cy * cz - sx * sy * sz,
    };
}

export function quaternionToEuler(
    x: number,
    y: number,
    z: number,
    w: number
): { x: number; y: number; z: number } {
    const x2 = x + x,
        y2 = y + y,
        z2 = z + z;
    const xx = x * x2,
        xy = x * y2,
        xz = x * z2;
    const yy = y * y2,
        yz = y * z2,
        zz = z * z2;
    const wx = w * x2,
        wy = w * y2,
        wz = w * z2;

    const m13 = xz + wy;
    const ey = Math.asin(m13 < -1 ? -1 : m13 > 1 ? 1 : m13);

    if (m13 > -0.9999999 && m13 < 0.9999999) {
        return {
            x: Math.atan2(wx - yz, 1 - (xx + yy)) * RAD_TO_DEG,
            y: ey * RAD_TO_DEG,
            z: Math.atan2(wz - xy, 1 - (yy + zz)) * RAD_TO_DEG,
        };
    } else {
        return {
            x: Math.atan2(yz + wx, 1 - (xx + zz)) * RAD_TO_DEG,
            y: ey * RAD_TO_DEG,
            z: 0,
        };
    }
}

export function lookAt(
    eyeX: number,
    eyeY: number,
    eyeZ: number,
    targetX: number,
    targetY: number,
    targetZ: number,
    upX = 0,
    upY = 1,
    upZ = 0
): { x: number; y: number; z: number; w: number } {
    if (
        !Number.isFinite(eyeX) ||
        !Number.isFinite(eyeY) ||
        !Number.isFinite(eyeZ) ||
        !Number.isFinite(targetX) ||
        !Number.isFinite(targetY) ||
        !Number.isFinite(targetZ)
    ) {
        throw new Error(
            `lookAt received NaN: eye=[${eyeX},${eyeY},${eyeZ}], target=[${targetX},${targetY},${targetZ}]`
        );
    }

    let zx = eyeX - targetX;
    let zy = eyeY - targetY;
    let zz = eyeZ - targetZ;
    let zLen = Math.sqrt(zx * zx + zy * zy + zz * zz);

    if (zLen === 0) {
        zz = 1;
    } else {
        zLen = 1 / zLen;
        zx *= zLen;
        zy *= zLen;
        zz *= zLen;
    }

    let xx = upY * zz - upZ * zy;
    let xy = upZ * zx - upX * zz;
    let xz = upX * zy - upY * zx;
    let xLen = Math.sqrt(xx * xx + xy * xy + xz * xz);

    if (xLen < 1e-6) {
        if (Math.abs(zz) > Math.abs(zx)) {
            upX += 1e-4;
        } else {
            upZ += 1e-4;
        }
        xx = upY * zz - upZ * zy;
        xy = upZ * zx - upX * zz;
        xz = upX * zy - upY * zx;
        xLen = Math.sqrt(xx * xx + xy * xy + xz * xz);
    }

    if (xLen < 1e-6) {
        xx = 1;
        xy = 0;
        xz = 0;
    } else {
        xLen = 1 / xLen;
        xx *= xLen;
        xy *= xLen;
        xz *= xLen;
    }

    const yx = zy * xz - zz * xy;
    const yy = zz * xx - zx * xz;
    const yz = zx * xy - zy * xx;

    const trace = xx + yy + zz;
    let qw: number, qx: number, qy: number, qz: number;

    if (trace > 0) {
        const s = 0.5 / Math.sqrt(trace + 1);
        qw = 0.25 / s;
        qx = (yz - zy) * s;
        qy = (zx - xz) * s;
        qz = (xy - yx) * s;
    } else if (xx > yy && xx > zz) {
        const s = 2 * Math.sqrt(1 + xx - yy - zz);
        qw = (yz - zy) / s;
        qx = 0.25 * s;
        qy = (yx + xy) / s;
        qz = (zx + xz) / s;
    } else if (yy > zz) {
        const s = 2 * Math.sqrt(1 + yy - xx - zz);
        qw = (zx - xz) / s;
        qx = (yx + xy) / s;
        qy = 0.25 * s;
        qz = (yz + zy) / s;
    } else {
        const s = 2 * Math.sqrt(1 + zz - xx - yy);
        qw = (xy - yx) / s;
        qx = (zx + xz) / s;
        qy = (yz + zy) / s;
        qz = 0.25 * s;
    }

    return { x: qx, y: qy, z: qz, w: qw };
}
