import type { State } from '@multiplekex/shallot';

export function setupCanvas(canvas: HTMLCanvasElement): void {
  const rect = canvas.getBoundingClientRect();
  canvas.width = rect.width * window.devicePixelRatio;
  canvas.height = rect.height * window.devicePixelRatio;
}

export function setupVisibilityControl(state: State): void {
  new IntersectionObserver(([entry]) => {
    if (entry.isIntersecting) {
      state.start();
    } else {
      state.stop();
    }
  }).observe(document.documentElement);
}

export function createVisibilityLoop(state: State, onTick?: (dt: number) => void): void {
  let running = false;
  let lastTime = performance.now();

  function tick(now: number): void {
    if (!running) return;
    const dt = (now - lastTime) / 1000;
    lastTime = now;
    state.step(dt);
    onTick?.(dt);
    requestAnimationFrame(tick);
  }

  new IntersectionObserver(([entry]) => {
    if (entry.isIntersecting) {
      if (!running) {
        running = true;
        lastTime = performance.now();
        requestAnimationFrame(tick);
      }
    } else {
      running = false;
    }
  }).observe(document.documentElement);
}

export function setupScrollPassthrough(canvas: HTMLCanvasElement): void {
  canvas.addEventListener(
    'wheel',
    (e) => {
      window.parent.postMessage({ type: 'iframe-scroll', deltaY: e.deltaY }, '*');
    },
    { passive: true }
  );
}

export function setupIframeScrollListener(): void {
  window.addEventListener('message', (e) => {
    if (e.data?.type === 'iframe-scroll' && typeof e.data.deltaY === 'number') {
      window.scrollBy(0, e.data.deltaY);
    }
  });
}
